import React from "react"
import "../styles/card.css"

const Card = ({ id, title, description, imageSrc, github, website }) => {
    return (
        <div style={{ display: "flex" }} key={id}>
            {/* <div style={{ width: "200px" }}>
                <div
                    className="card"
                    style={{
                        backgroundImage:
                            "linear-gradient(to bottom, rgba(245, 246, 252, 0), rgba(0, 0, 0, 0.2)),url(" +
                            imageSrc +
                            ")",
                    }}
                >
                </div>
            </div> */}
            <div style={{width: "30%", height: "250px"}}>
                <img src={imageSrc} style={{width: "100%"}}></img>
            </div>
            <div style={{width: "65%"}}>
                <h2>{title}</h2>
                {description.map(para => (
                    <p>{para}</p>
                ))}
                <a href={github ? github : "#"} target="_blank" rel="noopener noreferrer" className="btn">GitHub</a>
                <a href={website ? website : "#"} target="_blank" rel="noopener noreferrer" className="btn">Website</a>
            </div>
        </div>
    )
}

export default Card



