import Typewriter from 'typewriter-effect';

import resume from "../pdf/resume.pdf"
import data from "../data";

import "../styles/about.css"
import "../styles/intro.css"

const Intro = () => {
  const what_i_am = ["Software Engineer", "Data Scientist", "Web Developer", "Open Source Contributor"]

  return (
    <div className="section" id="home" style={{ height: "calc(100vh - 50px)", background: "#eae9e9" }}>
      <div className="container" style={{ alignItems: "start", justifyContent: "initial", width: "80vw", marginLeft: "10vw", marginTop: "30px"}}>
        <div style={{ width: "100%" }}>
          <h1 style={{ textAlign: "center" }}>
            Hi, my name is Guan Quan
          </h1>
          <div style={{ height: "20vh" }}>
            <h1 style={{ fontSize: "48px" }}>I am a
              <Typewriter
                options={{
                  strings: what_i_am,
                  delay: 65,
                  deleteSpeed: 65,
                  autoStart: true,
                  pauseFor: 900,
                  loop: true,
                }}
              />
            </h1>
          </div>
          <div>
            <p>A 21 year old programmer who enjoys coding</p>
            <a href={resume} target="_blank" rel="noopener noreferrer" className="btn">Resume</a>
          </div>
        </div>
        {/* <div className="image-wrapper">
          <img src={data.aboutImage} alt="about" className="about_img"></img>
        </div> */}
      </div>
    </div>
  )
}

export default Intro
